// index.js
import 'ol/ol.css';
import * as ol from 'ol'; //  OpenLayers 
import Map from 'ol/Map';
import View from 'ol/View';
import Tile from 'ol/layer/Tile';
import layerVector from 'ol/layer/Vector';
import sourceVector from 'ol/source/Vector'
import XYZ from 'ol/source/XYZ';
import Overlay from 'ol/Overlay';
import { fromLonLat, transform } from 'ol/proj';
import Select from 'ol/interaction/Select';
/**coordinates
 *  OpenLayers 
 */
class olMaps {

  /**
   * @param {string} targetId - HTML  ID
   * @param {Object} [options] - 
   * @param {Array<number>} [options.center=[0, 0]] - , 
   * @param {number} [options.zoom=2] - 
   */
  static gaodeUrl="";
  
  constructor(targetId, options = {}) {
    this.map=null;//
    this.mapType=""//
    this.mapSatellite=false//
    this.mapOnline=true
    this.mapUrl=""
    this.ol=ol;//ol
    this.overlay=null;//
    this.targetId = null;//  ID
    this.mapCenter = [103, 30];
    this.mapZoom = 9;//
    this.maxZoom=19;//
    this.minZoom=2;//
    this.mapLayers =[];
    this.mapLayer=null;//
    
  }
  /**
   * 
   * @param {*string} targetId id
   * @param {*string} mapType  baidu gaode google custom
   * @param {*boolean} mapSatellite  true false
   * @param {*boolean} mapOnline  true false
   * @param {*object} options  - 
   * @param {*string} [options.mapUrl] 
   * @param {*string} [options.mapLayerSource]
   * @param {*Array} [options.mapCenter] 
   * @param {*number} [options.mapZoom] 
   * @param {*number} [options.maxZoom] 
   * @param {*number} [options.minZoom] 
   */
  initMap(targetId,mapType,mapSatellite,mapOnline,options={}) {
    this.targetId = targetId;
    this.mapType=mapType||gaode;
    this.mapOnline=mapOnline!==undefined?mapOnline:true;
    this.mapSatellite=mapSatellite||false;
    this.mapCenter = options.mapCenter || [103, 30];
    this.mapZoom=options.mapZoom||9;
    this.maxZoom=options.maxZoom||19;
    this.minZoom=options.minZoom||2;
    if(mapType=="custom"){
      this.mapLayer=new Tile({
        source: mapLayerSource
      })
    }else{
      var mapurl=this.getMapUrl(options)
      this.mapLayer=new Tile({
        source:new XYZ({
          url:mapurl
        })
      })
    }
    
    console.log(this.mapLayer)
    this.map = new Map({
      target: this.targetId,
      layers: [
        this.mapLayer
      ],
      view: new View({
        center: fromLonLat(this.mapCenter),
        zoom: this.mapZoom,
        minZoom: this.minZoom,
        maxZoom: this.maxZoom,
      })
    });
  }
  getMapUrl(options){
    var mapUrls={
      "baidu":"",
      "gaode":'http://wprd0{1-4}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=7&x={x}&y={y}&z={z}',
      "google":'http://www.google.com/maps/vt/pb=!1m4!1m3!1i{z}!2i{x}!3i{y}!2m3!1e0!2sm!3i380072576!3m8!2sen-US!3scn!5e1105!12m4!1e68!2m2!1sset!2sRoadmap!4e0!5m1!1e0',
      "baiduSatellit":"",
      "gaodeSatellit":"http://wprd0{1-4}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=6&x={x}&y={y}&z={z}",
      "googleSatellit":"https://www.google.com/maps/vt?lyrs=y&hl=en&x={x}&y={y}&z={z}"
    }
    var urls=""
    // if(options.mapUrl){
    //   return options.mapUrl
    // }
    if(this.mapOnline){
      var names=this.mapType
      if(this.mapSatellite){
        names=names+"Satellit"
      }
      urls=mapUrls[names]
    }else {
      urls=options.mapUrl+'/{z}/{x}/{y}.png'
    }
    return urls
  }
  /**
   * 
   * @param {Array<number>} coordinates - , 
   */

  setCenter(coordinates) {
    const view = this.map.getView();
    view.setCenter(fromLonLat(coordinates));
  }

  /**
   * XYZ
   * @param {*object} options - 
   * @param {*string} options.url  URL
   * @param {*string} options.attributions 
   * @param {*number} options.maxZoom 
   * @param {*number} options.minZoom 
   * @param {*function} options.tileLoadFunction 
   * @param {*number} options.tilePixelRatio 
   * @param {*number} options.transition 
   * @param {*Projection} options.projection 
   */
  XYZ(options){
    return new XYZ(options)
  }

  /**
   * 
   * @param {*object} options - 
   * @param {*string} options.source  ol/source/Vector 
   * @param {*object} options.style 
   * @param {*number} options.zIndex  zIndex 
   * @param {*boolean} options.visible  
   * @param {*number} options.opacity  0 1
   * @param {*number} options.renderMode  'vector' 'image'
   */
  layerVector(options){
    if(options==""||options==null||options==undefined||typeof options == "object"&& JSON.stringify(obj) === '{}'){
      return new layerVector()
    }else{
      return new layerVector(options)
    }
  }

  /**
   * 
   * @param {*object|{}} options - 
   * @param {*Array.<ol.Feature>|undefined} options.features 
   * @param {*ol.format.Feature|undefined} options.format  ol.format.GeoJSON
   * @param {*ol.FeatureLoader|undefined} options.loader 
   * @param {*string|undefined} options.url  URL  AJAX 
   * @param {*function} options.strategy  ol/loadingstrategy.bbox
   * @param {*boolean|undefined} options.wrapX 
   */
  sourceVector(options){
    if(options==""||options==null||options==undefined||typeof options == "object"&& JSON.stringify(obj) === '{}'){
      return new sourceVector()
    }else{
      return new sourceVector(options)
    }
  }

  /**
   * 
   * @param {*Array<number>} coordinates 
   */
  fromLonLat(coordinates, projection = 'EPSG:4326'){
    return fromLonLat(coordinates,projection)
  }
  
  /**
   * 
   * @param {*Array<number>} coordinates 
   * @param {*} source   'EPSG:3857'Web Mercator
   * @param {*} destination  'EPSG:4326'WGS84
   * @returns 
   */
  transform(coordinates, source = 'EPSG:3857', destination = 'EPSG:4326'){
    return transform(coordinates,source,destination)
  }

  /**
   * 
   */
  switchToSatellite() {
      this.map.getLayers().item(0).setSource(
          new XYZ({
          url: 'https://{a-c}.tile.stamen.com/terrain/{z}/{x}/{y}.png'
          })
      );
  }
  /**
   * 
   * @param {Function} callback - 
   */
  addClickListener(callback) {
    this.map.on('click', (event) => {
      const coordinate = event.coordinate;
      callback(coordinate);
    });
  }
    // Method to listen to map click events
    onMapClick(callback) {
        this.selectInteraction.on('select', (event) => {
        const features = event.target.getFeatures();
        callback(features);
        });
    }

  /**
   * 
   * @param {Array<number>} coordinate - , 
   * @param {string} content - 
   */
  showPopup(coordinate, content) {
    if (this.overlay) {
      this.map.removeOverlay(this.overlay);
    }

    const element = document.createElement('div');
    element.className = 'popup';
    element.innerHTML = content;

    this.overlay = new Overlay({
      element: element,
      positioning: 'bottom-center',
      stopEvent: false,
      offset: [0, -10]
    });

    this.map.addOverlay(this.overlay);
    this.overlay.setPosition(coordinate);
  }
}
// Compatibility for ES5 environments
if (typeof module !== 'undefined' && module.exports) {
    module.exports = olMaps;
  }

export default olMaps;
